.page {
    margin-top: 80px;
    font-family: 'Literata', serif;
    font-size: 18px;
}

.section1 {
    display: grid;
    grid-template-columns: 2fr 3fr;
    gap: 60px;
    max-width: 960px;
    margin: 0 auto;
}
.section1 h1 {
    text-align: end;
    text-transform: uppercase;
    font-size: 18px;
    margin-top: 0;
    margin-bottom: 1rem;
    font-weight: 400;
    line-height: 1.5;
}
.image1 {
    max-width: 100%;
}
.section1text {
    margin-bottom: 30px;
}

.section2 {
    display: grid;
    grid-template-columns: 3fr 2fr;
    gap: 60px;
    max-width: 1100px;
    margin: 140px auto;
}

.section2imagesGrid {
    display: grid;
    grid-template-columns: 3fr 2fr;
    gap: 20px;
    max-width: 100%;
}

.section2imagesGridColumn1 img {
    display: block;
    max-width: 100%;
    margin-left: auto;
}
.section2imagesGridColumn1 img:first-child {
    max-width: 66%;
    margin-bottom: 20px;
}
.section2imagesGridColumn2 img {
    display: block;
    max-width: 100%;
}
.section2imagesGridColumn2 img:first-child {
    margin-bottom: 20px;
}
.section2textSection {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
}
.section2textSection p:last-child {
    margin-bottom: 0;
}

.section3 {
    max-width: 1100px;
    margin: 140px auto;
}

.section3line1 {
    display: grid;
    grid-template-columns: 3fr 3fr 4fr;
    gap: 20px;
}
.section3line1 img {
    max-width: 100%;
}
.section3line2 {
    display: grid;
    grid-template-columns: 3fr 4fr 3fr;
    gap: 20px;
    margin-top: 20px;
}
.section3line2 img {
    max-width: 100%;
}

.section4 {
    max-width: 1000px;
    margin: 80px auto 0;
}
.section4 > div {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 80px;
}
.section4 > p {
    margin-top: 20px;
    text-align: end;
}
.megagrid {
    position: relative;
    margin: 120px auto;
}
.megagrid img {
    max-width: 100%;
}
.video {
    position: absolute;
    top: 35.5%;
    left: 22.5%;
    height: 20%;
    width: 14.6%;
}
.muted {
    color: #A3A3A3;
}
.close {
    height: 25px;
    width: 25px;
    position: fixed;
    top: 35px;
    right: 35px;
    opacity: 0.25;
    transition: opacity 0.2s ease-in-out;
    z-index: 10;
}
.close:hover {
    opacity: 1;
}